let consturl;
export default {
	data() {
		return {
		   ServiceNum:"",
		   openid:"",
		   getBulltin:""
		}
	},
	onLoad(options) {
		let ts=this;
		ts.openid=uni.getStorageSync("openid")
		// let ts=this,id=uni.getStorageSync("openid");
		// consturl=ts.$consturl;
		// ts.openid=id;
		// if(ts.openid!=""&&ts.openid!=null&&ts.openid!=undefined){
		// uni.request({
		// 	url: consturl + "wx/api/ServiceNum", //
		// 	data: {
		// 		openid:ts.openid
		// 	},
		// 	success: function (res) {
		// 			console.log(ts.ServiceNum=res.data.data);
		// 	},
		// 	complete:function(){
		// 		console.log("complete");
		// 	}
		// });
		// }
		// 获取工作人员的公告信息
		uni.request({
			url: "https://www.ccweb.xyz/gas/wx/api/getBulltin", //
			data: {
				type:"staff"
			},
			success: function (res) {
					console.log(res.data);
					ts.getBulltin=res.data.data;
					// console.log(data);
			},
			complete:function(){
				// console.log("complete");
			}
		});
	},
	methods: {
		// 检查是否登录
		toPartyBuild:function(){
			if(this.openid!=''&&this.openid!=undefined){
				uni.navigateTo({
				    url: '../partyBuild/selector'
				});
			}else{
				uni.showToast({
				    title: '检查是否登录',
				    duration: 1000
				});
			}
			
		},
		// 去注册
		registe:function(){
			uni.navigateTo({
			    url: 'register/register'
			});
			return  false;
		},
		// 是否注销账号
		click: function(e) {
			uni.showModal({
			    title: '提示',
			    content: '是否注销账号',
			    success: function (res) {
			        if (res.confirm) {
			            uni.setStorageSync("openid","");
						uni.setStorageSync("identity","");
						uni.navigateTo({
							url:"../index/index"
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
